body {
    margin: 0;
    padding: 0;
    font-family: 'Source Sans Pro', sans-serif;
    min-width: 100%;
    min-height: 100vh;
    font-weight: 300;
    -webkit-text-size-adjust: none;
    background: #F7F7F7;
}

*,
*:before,
*:after {
    box-sizing: border-box
}

/* Reset */
html {
    font-size: 18pt;
}

@media (max-width: 1680px) {
    html {
        font-size: 13pt;
    }
}

@media (max-width: 1280px) {
    html {
        font-size: 13pt;
    }
}

@media (max-width: 980px) {
    html {
        font-size: 11pt;
    }
}

@media (max-width: 736px) {
    html {
        font-size: 11pt;
    }
}

img {
    width: 100%;
    max-width: 100%;
    display: inline-block;
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 1rem;
    margin-bottom: 1.5rem;
}

p,
hr {
    margin-top: 1rem;
    margin-bottom: 1rem;
}
.open-sans{
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
}
.roboto{
    font-family: 'Roboto', sans-serif;
}
.mbold{font-weight: 500}
.bold{font-weight: 700}

/* main css */

#page-wrapper {
    -webkit-overflow-scrolling: touch;
    display: flex;
    -moz-flex-direction: column;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -moz-align-items: center;
    -webkit-align-items: center;
    -ms-align-items: center;
    align-items: center;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    -ms-justify-content: center;
    justify-content: center;
    min-height: 100vh;
    position: relative;
    z-index: 2;
    overflow: hidden;
}

.inner-wrapper {
    position: relative;
    z-index: 1;
    border-radius: inherit;
    padding: 4rem 3rem;
    max-width: 100%;
    width: 61rem;
}

.container > .container-wrap, .container2 > .container-wrap {
    -moz-flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    display: inline-flex;
    -moz-align-items: flex-start;
    -webkit-align-items: flex-start;
    -ms-align-items: flex-start;
    align-items: flex-start;
    
    vertical-align: top;
    position: relative;
    max-width: 100%;

}

.container > .container-wrap{
    padding: 4.5rem 4.5rem;
    width: 80rem;
    background-color: #FFFFFF;
    border-radius: 0.75rem;
}
@media(max-width:736px){
    .inner-wrapper {
        padding: 1rem 1rem;}
    .container > .container-wrap{
        padding: 3.5rem 2rem;
    }
}

.container > .container-wrap > .col, .container2 > .container-wrap > .col {
    -moz-flex-shrink: 0;
    -webkit-flex-shrink: 0;
    -ms-flex-shrink: 0;
    flex-shrink: 0;
    -moz-flex-grow: 0;
    -webkit-flex-grow: 0;
    -ms-flex-grow: 0;
    flex-grow: 0;
    max-width: 100%;
  
}
.container > .container-wrap > .col{
     padding: 0 0 0 9rem; 
}
.container > .container-wrap > .col > *, .container2 > .container-wrap > .col > *{
    margin-top:1.5rem;
    margin-bottom: 1.5rem;
}
.container > .container-wrap > .text-col {
    border-top-left-radius: inherit;
    margin-left: -9rem;
    width: calc(50% + 4.5rem);
    text-align: left;
}
.text-col > .heading1{
    color: rgba(0,0,0,0.671);
    letter-spacing: -0.15rem;
    font-size: 3.25em;
    line-height: 1.125;
    margin-top: 0!important;
}
.text-col > .subhead {
    color: #1FBF99;
    letter-spacing: -0.05rem;
    font-size: 1.25em;
    line-height: 1.375;
}
.text-col > .text1{
    color: rgba(0,0,0,0.322);
    letter-spacing: -0.025rem;
    font-size: 1em;
    line-height: 1.625;
     margin-bottom: 0!important;
}
@media (max-width: 736px){
    .container > .container-wrap{
    padding: 3.5rem 2rem;
    }
    .container > .container-wrap > .col{
    padding: 3rem 0 !important; 
        text-align: center;}
    .container > .container-wrap > .text-col {
    width: 100% !important;
    min-height: 100% !important;
    text-align: center!important;
        margin-left: 0 !important;
    padding-top: 0 !important;}
    
    .text-col > .heading1{
        font-size: 3em;
        letter-spacing: -0.13125rem;
    }  
}
    @media (max-width: 480px){
        .container > .container-wrap > .col > *, .container2 > .container-wrap > .col > *{
    margin-top:1.3125rem;
    margin-bottom: 1.3125rem;
}     
    }
        @media (max-width: 360px){
            .container > .container-wrap{
        padding: 2.625rem 1.5rem;
    }
        .container > .container-wrap > .col > *, .container2 > .container-wrap > .col > *{
   margin-top: 1.125rem;
    margin-bottom: 1.125rem;
}     
    }
    
 

.container > .container-wrap > .signup-col{
    border-top-right-radius: inherit;
    width: calc(50% + 4.5rem);
    text-align: center;
}

.container > .container-wrap > .signup-col:before {
    content: '';
    display: block;
    position: absolute;
    width: 0;
    margin-left: calc(-4.5rem - 0.5px);
    border-left: 1px solid  rgba(0,0,0,0.102);
    top: 0;
    height: 100%;
}

.signup-col > .heading2{
    color: rgba(0,0,0,0.671);
    letter-spacing: -0.05rem;
    font-size: 1.75em;
    line-height: 1;
    margin-top: 0!important;
}
.signup-col > .text2{
color: rgba(0,0,0,0.322);
    letter-spacing: -0.025rem;
    font-size: 1em;
    line-height: 1.625;
}
#signup-form > .form-wrap{
    width:25rem;
    display: inline-flex;
    max-width: 100%;
    -moz-flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -moz-flex-direction: column;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
}

#signup-form > .form-wrap > .form-field{
    margin-top:1rem;
}
#signup-form > .form-wrap > .form-field:first-child{
    margin-top:0;
}
#signup-form > .form-wrap > .form-field > .form-control{
    width: 100%;
    height: 3rem;
    line-height: 3rem;
    padding: 0 1.05rem;
    color: rgba(0,0,0,0.322);
    border: solid 1px rgba(0,0,0,0.149);
    font-size: 1em;
    font-family: 'Open Sans', sans-serif;
    letter-spacing: -0.025rem;
    border-radius: 0.25rem;
    outline: none
}
#signup-form > .form-wrap > .form-field > .form-control:focus{
    border-color: #2FD6AF;
    box-shadow: 0 0 0 1px #2FD6AF;
}
#signup-form ::-webkit-input-placeholder {
    color: rgba(0, 0, 0, 0.322);
    opacity: 0.55;
}

#signup-form :-moz-placeholder {
    color: rgba(0, 0, 0, 0.322);
    opacity: 0.55;
}

#signup-form ::-moz-placeholder {
    color: rgba(0, 0, 0, 0.322);
    opacity: 0.55;
}

#signup-form :-ms-input-placeholder {
    color: rgba(0, 0, 0, 0.322);
    opacity: 0.55;
}
#signup-form > .form-wrap > .form-field > .btn{
    height: 3rem;
    line-height: 3rem;
    padding: 0 1.5rem;
    font-size: 1em;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 700;
    border-radius: 0.25rem;
    background-color: #1FBF99;
    color: #FFFFFF;
    position: relative;
    border: 0;
    cursor: pointer;
    text-align: center;
    max-width: 100%;
    white-space: nowrap;
    -moz-transition: opacity 0.35s ease, color 0.25s ease, background-color 0.25s ease, border-color 0.25s ease;
    -webkit-transition: opacity 0.35s ease, color 0.25s ease, background-color 0.25s ease, border-color 0.25s ease;
    -ms-transition: opacity 0.35s ease, color 0.25s ease, background-color 0.25s ease, border-color 0.25s ease;
    transition: opacity 0.35s ease, color 0.25s ease, background-color 0.25s ease, border-color 0.25s ease;
    -moz-display: inline-flex;
    -webkit-display: inline-flex;
    -ms-display: inline-flex;
    display: inline-flex;
    -moz-align-items: center;
    -webkit-align-items: center;
    -ms-align-items: center;
    align-items: center;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    -ms-justify-content: center;
    justify-content: center;
    vertical-align: middle;
}
#signup-form > .form-wrap > .form-field > .btn:hover {
    background-color: #2FD6AF;
}
.signup-col > .text3{
    color: rgba(0,0,0,0.2);
    letter-spacing: -0.025rem;
    width: 100%;
    font-size: 0.875em;
    line-height: 1.625;
    margin-top: 1.5rem;
    margin-bottom: 0!important;
}
@media(max-width:736px){
    .container > .container-wrap > .signup-col{
    width: 100% !important;
    min-height: 100% !important;
}
    .container > .container-wrap > .signup-col:before {
         border-top: 1px solid  rgba(0,0,0,0.102);
         border-left: 0;
        margin-left: 0 !important;
        margin-top: calc(-3rem - 0.5px) !important;        
    top: auto !important;
    height: 0 !important;
        width:100%;
        left: 0;
    }
      .container > .container-wrap > .signup-col{
          padding-bottom: 0 !important;
    }

}

.container2 {
    display: flex;
    width: 100%;
    -moz-align-items: center;
    -webkit-align-items: center;
    -ms-align-items: center;
    align-items: center;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    -ms-justify-content: center;
    justify-content: center;
    background-color: transparent;
    margin-top:1.5rem;
}
.container2 > .container-wrap {
    padding: 0rem 0rem;
    width: 100%;
    max-width: 61rem;
    vertical-align: top;
    position: relative;
    max-width: 100%;
    border-radius: inherit;
}
.container2 > .container-wrap > .col{
     padding: 0 0 0 0; 
    margin-bottom: 0;
}

.container2 > .container-wrap > .social-box{
        width: 40%;
    text-align: left;
        margin-left: 0rem;
}
.social {
    list-style: none;
    margin: 0 0 0 -0.375rem!important;
    padding: 0;
    font-size: 1rem;
}

.social li {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    z-index: 1;
    margin: 0.375rem;
}

.social a.icon {
    display: block;
    border: solid 1px #E4EBF0;
    border-radius: 100%;
    width: 2em;
    height: 2em;
    line-height: 2rem;
    display: flex;
    -moz-align-items: center;
    -webkit-align-items: center;
    -ms-align-items: center;
    align-items: center;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    -ms-justify-content: center;
    justify-content: center;
}
.social a.icon svg {
    fill: rgba(0,0,0,0.322);
    width: 60%;
     height: 60%;
}
.social a.icon:hover {
    border-color: #2FD6AF ;
}

.social a.icon svg {
    display: block;
    margin: 0 auto;
    position: relative;
    -moz-transition: fill 0.25s ease;
    -webkit-transition: fill 0.25s ease;
    -ms-transition: fill 0.25s ease;
    transition: fill 0.25s ease;

}
.social a.icon:hover svg {
    fill: #2FD6AF;
}
.container2 > .container-wrap > .copyright{
        width: 60%;
    text-align: right;
}
.copyright-txt{
        color: rgba(0,0,0,0.2);
    letter-spacing: -0.025rem;
    width: 100%;
    font-size: 0.875em;
    line-height: 1.625;
    margin-bottom: 0!important;
    margin-top:0!important;
}

@media(max-width:736px){
    .container2 > .container-wrap > .social-box,.container2 > .container-wrap > .copyright{
        width: 100% !important;
    min-height: 100% !important;
        text-align: center!important;
        padding: 0.375rem 0 !important;
    }
    .container2 > .container-wrap > .social-box{
        margin-left:0;
        padding-top:0!important
    }
}
@media(max-width:480px){
    .container2 {
        margin-top: 1.3125rem;
    }
}
@media(max-width:360px){
    .container2 {
        margin-top:1.125rem;
    }
}
.error{ text-align: left;
    font-size: .85em;
    display: block;
    color: rgba(255, 0, 0, .6);
    margin-top: 5px;}
